  cursor: pointer;
  text-decoration: none;
  font-size: 1rem;
  /* flex-grow: 1; */
}

.nav-logo:hover {
  color: #ffdd40;
}

.nav-menu {
  display: -webkit-flex;
  display: flex;
  list-style: none;
  text-align: center;
  margin-right: 2rem;
}

.nav-links {
  color: #fff;
  text-decoration: none;
  padding: 0.5rem 1rem;
  height: 100%;
  border-bottom: 3px solid transparent;
}
.fa-code {
  margin-left: 1rem;
}

.nav-item {
  line-height: 40px;
  /* margin-right: 1rem; */
}

.nav-item:after {
  content: "";
  display: block;
  height: 3px;
  width: 0;
  background: transparent;
  transition: width 0.7s ease, background-color 0.5s ease;
}

.nav-item:hover:after {
  width: 100%;
  background: #ffdd40;
}

.nav-item .active {
  color: #fff;
  /* border: 1px solid #ffdd40; */
}

.nav-icon {
  display: none;
}

