.header {
  padding: 50px 15px;
  background-color: $bg-color;
  text-align: center;
  margin-bottom: 40px;

  h1 {
    font-size: 40px;
    font-weight: 300;
  }
}

.table-container {
  max-width: 1200px;
  width: 100%;
  margin: 0 auto 40px;

  &__title {
    background-color: $highlight-color;
    color: $light-color;
    text-align: center;
    padding: 10px;

    h2 {
      font-size: 24px;
      font-weight: 300;
    }
  }

  &__table {
    width: 100%;
    border-collapse: collapse;

    thead {
      tr {
        background-color: transparent;
      }
    }

    td,
    th {
      border: 1px solid $border-color;
      padding: 10px;
      text-align: center;
    }

    tr {
      &:nth-child(even) {
        background-color: $bg-color;
      }
    }

    &--break-lg {
      @media (max-width: 991px) {
        @include break;
      }
    }

    &--break-md {
      @media (max-width: 767px) {
        @include break;
      }
    }

    &--break-sm {
      @media (max-width: 575px) {
        @include break;
      }
    }
  }
}
